import { ApolloServer, gql } from 'apollo-server-express';
// import typeDefs from './typedefs';
// import resolvers from './resolvers';
const typeDefs = gql `
  type Query {
    hello: String
  }
`;

const resolvers = {
    Query: {
        hello: () => 'Hello world!'
    },
};

console.log('create schema');
const schema = {
    typeDefs,
    resolvers
};

export default schema;